
import { mapGetters } from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import { changeUserInfo } from '../../api'
import { msgFormatter } from '../../utils'
import lrz from 'lrz'
export default {
name: 'ImageEditor',
components: {
Group,
CellBox,
Cell,
GroupTitle,
XButton,
Spinner
},
data () {
const avatar = this.$store.state.user.avatar
const showImg = avatar || ''
return {
selectedImg: '',
showImg,
originImg: showImg,
loading: false,
inputErrors: [],
errorMsg: '',
changed: false
}
},
computed: {
...mapGetters([
'user'
]),
hasChange () {
return this.originImg !== this.showImg
}
},
methods: {
syncImg (e) {
const file = e.target.files[0]
lrz(file).then(rst => {
const inputErrors = []
if (rst.fileLen > 1024 * 1024) {
inputErrors.push('图片过大，请选择较小的图片')
}
this.inputErrors = inputErrors
this.showImg = URL.createObjectURL(rst.file)
this.selectedImg = rst
})
},
submit () {
if (this.loading) {
return
}
let formData
if (!this.selectedImg) {
formData = {avatar: ''}
} else {
const inputErrors = []
if (this.selectedImg.file.size > 1024 * 1024) {
inputErrors.push('图片过大，请选择较小的图片')
this.inputErrors = inputErrors
return
} else {
formData = new FormData()
formData.append('avatar', this.selectedImg.file, this.selectedImg.origin.name)
}
}
this.loading = true
changeUserInfo(this.$store.state.user.id, formData).then((response) => {
this.changed = true
setTimeout(() => {
this.loading = false
this.$store.dispatch('fetchUser').then(() => {
this.$router.push({path: '/'})
})
}, 2000)
}, (response) => {
this.loading = false
this.errorMsg = msgFormatter(response)
})
},
resetImg () {
this.showImg = ''
this.selectedImg = ''
this.$refs.fileImgSend.value = ''
}
}
}



